body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* containers */
body{
  font-size: 1.5rem;
  background: url(/static/media/img3.908c114a.png) no-repeat center center;

}
.container-1{
  position: -webkit-sticky;
  position: sticky;
  position: relative;
  top: 100px;
  display: flex;
  flex-direction: column;
  }

/* inside container 1 */
@font-face {
  font-family: Monsterrat;
  src: url(/static/media/Montserrat-Regular.ee653992.ttf);
}
.text-1, .text-2, .images{
  margin: 3rem;
  align-self: flex-start;

  font-family: 'Monsterrat';
  color: azure;
  font-size: 30px;
  text-shadow: 3px 3px 10px #0a0324;
  
}
.socialLinks{
  position: relative;
  left: 45px;
  align-self: flex-start;
}
#pg-1, #pg-4{
  font-family: 'Monsterrat';
  color: azure;
}
#pg-2, .text-2, #pg-5, #pg-6{
  font-size: 60%;
}
#icon-1, #icon-2{
  top: -50px;
  height: 40px;
  width: 40px;
}
#img-1, #img-2{
  position: relative;
  top: 40px;
  object-fit: cover;
  margin: 2rem;
  height: 40%;
  width: 40%;
  box-shadow: 1px 1px 10px black;
}

#img-1:hover, #img-2:hover, #img-3:hover{
  box-shadow: 1px 1px 5px rgb(153, 117, 211);
}




